import React, { useState, useEffect } from 'react';
import Layout from '../common/Layout';
import '../style/ChatRoomStyle.css';
import * as ncloudchat from 'ncloudchat';



const ChatRoom = () => {
    const [ncloud, setNcloud] = useState('');

    const chatchk = async () => {
        const chat = new ncloudchat.Chat();
        chat.initialize("2eec90a6-59fb-4abd-99b4-f002d5d9a0cf");
        setNcloud(chat);
        console.log(ncloud);
    }

    useEffect(() => {
        chatchk()
    }, [])

    return (
        <Layout>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-sm-12">
                        <div id="user_chat_data" className="user_chat_data">
                            <div className="profile_name">
                                {/* <span>{username}</span> */}
                            </div>
                            <div className="container-fluid chat_section" id="chat-box">
                                {/* {messages.map((msg, index) => (
                                    <div key={index} className={msg.type === 'sent' ? "sent_msg" : "received_withd_msg"}>
                                        <p>{msg.msg}</p>
                                        <span className="time_date">{msg.createdAt} | {msg.sender}</span>
                                    </div>
                                ))} */}
                            </div>
                            <div className="type_msg" style={{ position: 'fixed', bottom: '70px', width: '600px' }}>
                                <div className="input_msg_write">
                                    <input
                                        type="text"
                                        className="write_msg"
                                        placeholder="Type a message"
                                    // value={outgoingMsg}
                                    // onChange={(e) => setOutgoingMsg(e.target.value)}
                                    // onKeyDown={handleKeyDown} 
                                    />

                                    <button
                                        className="msg_send_btn"
                                        type="button"
                                    // onClick={handleSendMessage}
                                    >
                                        <i className="fa fa-paper-plane" aria-hidden="true"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </Layout >
    );
};

export default ChatRoom;